export default {
    install(Vue) {

        /**
         * ,,
         */
        Vue.prototype.standLoginOut = function () {
            this.requestPost('/class_api/user/logout', null, function () {

            });
        };

        // 
        Vue.prototype.formatListData = function (response) {
            const result = {
                list: [],
                total: 0
            };
            const body = response.body;
            if (!body) {
                return result;
            }
            if (!body.success) {
                return result;
            }
            if (!body.data) {
                return result;
            }
            if (body.data.content.length == 0) {
                return result;
            }
            // 
            result.list = body.data.content;
            result.total = body.data.totalElements;
            return result;
        };

        // 
        Vue.prototype.formatNormalData = function (response) {
            const result = {
                success: false,
                data: {},
                errorMsg: ',',
                errorCode: '1001'
            };
            const body = response.body;
            if (!body) {
                return result;
            }
            if (!body.success) {
                result.errorMsg = body.errorMsg;
                result.errorCode = body.errorCode;
                return result;
            }
            result.success = true;
            result.errorMsg = body.errorMsg;
            result.data = body.data;
            result.errorCode = body.errorCode;
            if (!result.success) {
                result.success = false;
            }
            if (!result.errorMsg) {
                result.errorMsg = ',';
            }
            if (!result.errorCode) {
                result.errorCode = '1001';
            }
            if (!result.data) {
                result.data = {};
            }
            return result;
        };

        /**
         * post
         * @param url
         * @param data
         * @param callback
         */
        Vue.prototype.requestList = function (url, data, callBack) {
            this.$http.post(url, data).then(response => {
                const result = this.formatListData(response);
                callBack(result.total, result.list);
            }, response => {
                callBack(0, []);
            });
        };


        /**
         * put, success , data , errorMsg
         * @param url
         * @param data
         * @returns {Promise<unknown>}
         */
        Vue.prototype.requestPost = function (url, data, callBack) {
            console.log(':requestPost');
            this.$http.post(url, data).then(response => {
                const result = this.formatNormalData(response);
                console.log(':', result);
                callBack(result);
            }, response => {
                callBack({
                    success: false,
                    data: {},
                    errorMsg: ',',
                    errorCode: '1001'
                })
                ;
            });
        };

        /**
         * put, success , data , errorMsg
         * @param url
         * @param data
         * @returns {Promise<unknown>}
         */
        Vue.prototype.requestDelete = function (url, callBack) {
            console.log(':requestDelete');
            this.$http.delete(url).then(response => {
                const result = this.formatNormalData(response);
                console.log(':', result);
                callBack(result);
            }, response => {
                callBack({
                    success: false,
                    data: {},
                    errorMsg: ',',
                    errorCode: '1001'
                })
                ;
            });
        };
        /**
         * put, success , data , errorMsg
         * @param url
         * @param data
         * @returns {Promise<unknown>}
         */
        Vue.prototype.requestPut = function (url, data, callBack) {
            console.log(':requestPut');
            this.$http.put(url, data).then(response => {
                const result = this.formatNormalData(response);
                console.log(':', result);
                callBack(result);
            }, response => {
                callBack({
                    success: false,
                    data: {},
                    errorMsg: ',',
                    errorCode: '1001'
                })
                ;
            });
        };

        /**
         * put, success , data , errorMsg
         * @param url
         * @param data
         * @returns {Promise<unknown>}
         */
        Vue.prototype.requestGet = function (url,  callBack) {
            console.log(':requestGet');
            this.$http.get(url).then(response => {
                const result = this.formatNormalData(response);
                console.log(':', result);
                callBack(result);
            }, response => {
                callBack({
                    success: false,
                    data: {},
                    errorMsg: ',',
                    errorCode: '1001'
                })
                ;
            });
        };

        /**
         * 
         * @param url
         * @param data
         * @returns {Promise<unknown>}
         */
        Vue.prototype.requestFile = function (url, data) {
            console.log(':requestFile');
            this.showFullPageLoading();
            this.$http.post(url, data).then(response => {
                this.hideFullLoading();
                const result = this.formatNormalData(response);
                console.log(':', result);
                if (!result.success || result.data == null || this.strError(result.data.path)) {
                    this.showError(',');
                    return;
                }
                // pdf
                this.downloadFile(result.data.path);
            }, response => {
                this.hideFullLoading();
                this.showError(',');
            });
        };


        /**
         * delete, success , data , errorMsg
         * @param url
         * @param data
         * @returns {Promise<unknown>}
         */
        Vue.prototype.downloadFile = function (url) {
            const path = '/class_api/file/' + url;
            window.open(path);
        };
        /**
         * delete, success , data , errorMsg
         * @param url
         * @param data
         * @returns {Promise<unknown>}
         */
        Vue.prototype.getFileUrl = function (url) {
            if (!url) {
                return '';
            }
            const path = '/class_api/file/' + url;
            return path;
        };

        /**
         * pdf
         * @param url
         * @param data
         * @param callBack
         */
        Vue.prototype.standPrintFile = function (url, data, callBack) {
            this.$http.post(url, data).then(response => {
                var result = response.body;
                if (result == null) {
                    this.showError(',');
                    callBack();
                    return;
                }
                if (!result.success || result.data == null || this.strError(result.data.path)) {
                    this.showError(',');
                    callBack();
                    return;
                }
                // pdf
                this.downloadFile(result.data.path);
                callBack();
            }, response => {
                this.showError(',');
                callBack();
            });
        };

    }
};
