export default {
    install(Vue) {

        // url,
        Vue.prototype.showFullPageLoading = function () {
            this.$Spin.show({
                render: (h) => {
                    return h('div', [
                        h('Icon', {
                            'class': 'demo-spin-icon-load',
                            props: {
                                type: 'ios-loading',
                                size: 25
                            }
                        }),
                        h('div', '...')
                    ]);
                }
            });
            this.$Loading.config({
                color: '#515A6E',
                failedColor: '#f0ad4e',
            });
            this.$Loading.start();
        };
        Vue.prototype.hideFullLoading = function () {
            this.$Spin.hide();
            this.$Loading.finish();
        };


        /**
         * 
         * @param str
         */
        Vue.prototype.jumpMenu = function (str) {
            switch (str) {
                case '':
                    this.pushUrl('/custom');
                    break;
                case '':
                    this.pushUrl('/class');
                    break;
                case '':
                    this.pushUrl('/coach');
                    break;
            }
        };

        // 
        Vue.prototype.getSelectMenuName = function () {
            var queryTitle = this.$route.query.title;
            if (this.strError(queryTitle)) {
                return this.$route.name;
            }
            console.log(':' + queryTitle);
            return queryTitle;
        };

        // 
        Vue.prototype.refreshMenuName = function () {
            var name = this.getSelectMenuName();
            this.saveMenuName(name);
        };

        // url,
        Vue.prototype.pushUrl = function (url, query) {
            this.$router.push({
                path: url,
                query: query
            });
        };
    }
};
