export default {
    install(Vue) {


        /**
         * 
         * @param res
         */
        Vue.prototype.handleInfo = function (res, successMsg) {
            if (res.success&&successMsg) {
                this.showSuccess(successMsg);
                return;
            }
            if (!res.success) {
                this.showError(':' + res.errorMsg + ' :' + res.errorCode);
                return;
            }
        };

        // 
        Vue.prototype.showSuccess = function (msg) {
            try {
                this.$Message.destroy();
            } catch (e) {
            }
            this.$message({
                showClose: true,
                message: msg,
                type: 'success'
            });
        };

        // 
        Vue.prototype.showInfo = function (msg) {
            try {
                this.$message.destroy();
            } catch (e) {
            }
            if (this.strError(msg)) {
                return;
            }
            this.$message({
                showClose: true,
                message: msg
            });
        };

        // 
        Vue.prototype.showError = function (msg) {
            try {
                this.$Message.destroy();
            } catch (e) {
            }
            if (msg == null || msg == 'null') {
                return;
            }
            this.$message({
                showClose: true,
                message: msg,
                type: 'error'
            });
        };

    }
}
