        Vue.prototype.getSelects = function (type) {
            if (type) {
                return [];
            }
            var index = parseInt(type);
            if (!index) {
                return [];
            }
            var list = this.selects[index];
            if (!list) {
                return [];
            }
            return list;
        };

        Vue.prototype.saveSelects = function (type, list) {
            if (type) {
                return;
            }
            var index = parseInt(type);
            if (!index) {
                return;
            }
            if (list) {
                return;
            }
            this.selects[index] = list;
        };


