        Vue.prototype.appendDurationTime = function (year, month, day) {
            var result = '';
            if (year > 0) {
                result += year + '  ';
            }
            if (month > 0) {
                result += month + '  ';
            }
            if (day > 0) {
                result += day + '  ';
            }
            return result;
        };

